// Importation des dpendances : bibliothques et modules

// Bibliothque React pour la cration de composants et Hooks pour grer l'tat et les effets
import React, { useEffect, useState } from "react";
// Importation des images pour les icnes
import Edit from "../img/edit.png";
import Delete from "../img/delete.png";
// Fonctionnalits fournies par la bibliothque React Router pour la gestion des routes
import { Link, useLocation, useNavigate } from "react-router-dom";
// Importation du composant "Menu"
import Menu from "../components/Menu";
// Bibliothque pour effectuer des requtes HTTP
import axios from "axios";
// Bibliothque pour formater les dates
import moment from "moment";
// Hook pour accder au contexte
import { useContext } from "react";
// Contexte d'authentification import
import { AuthContext } from "../context/authContext";
// Bibliothque pour nettoyer et scuriser le HTML 
import DOMPurify from "dompurify";

/*
Affichage d'une page individuelle d'article 
contenant les informations de l'article, la possibilit de modifier ou de supprimer l'article, si l'utilisateur actuel est l'autreur de l'article, et une prsentation plus complte de l'article, accompagn d'une image.

Le composant "Single" renvoie un lment JSX qui reprsente la page individuelle de l'article. 

Le contenu de la page est divis en 2 parties principales : 

- l'image de l'article, des informations sur l'utilisateur qui a publi l'article : nom, image de profil, des boutons de modification et de suppression de l'article s'ils sont disponible pour l'utilisateur actuel le titre de l'aricle, la description de l'article 
- Menu qui prend en charge la catogorisation de l'article
*/

// Dclaration des constante et des tats
const Single = () => {
  // Dclaration de l'tat "post"", utilis pour stocker les donnes de l'article qu'on va rcuprer
  const [post, setPost] = useState({});

  const location = useLocation();
  const navigate = useNavigate();

  // Extraction de l'id de l'article  partir de l'url (on rcupre postId)
  const postId = location.pathname.split("/")[2];

  // Utilisation du contexte d'authentification
  const { currentUser } = useContext(AuthContext);

  const formatTimeSincePublication = (dateString) => {
    const currentDate = new Date();
    const publicationDate = new Date(dateString);

    const timeDifference = currentDate - publicationDate;
    const daysDifference = Math.floor(timeDifference / (1000 * 60 * 60 * 24));
    const monthsDifference = Math.floor(daysDifference / 30);

    if (monthsDifference >= 1) {
      return `${monthsDifference} mois`;
    } else {
      return `${daysDifference} jours`;
    }
  };

  /*  
  Permet de dfinir un effet qui sera excut une fois que le composant est mont, il utilise Axios pour effectuer une requte HTTP GET, pour rcuprer les donnes de l'articles avec l'id correspondant depuis l'API. Les donnes sont stockes dans l'tat local "post"  l'aide la fonction "setPost"
  */

  // Version locale 

  // useEffect(() => {
  //   const fetchData = async () => {
  //     try {
  //       const res = await axios.get(`/posts/${postId}`);
  //       setPost(res.data);
  //     } catch (err) {
  //       console.log(err);
  //     }
  //   };
  //   fetchData();
  // }, [postId]);

  // Version serveur

  useEffect(() => {
    const fetchData = async () => {
      try {
        let url = `http://gabcassblog.gabriel-cassano.be/api/posts/${postId}`;        
                
        const res = await axios.get(url);
  
        if (!res.headers['content-type'] || !res.headers['content-type'].includes('application/json')) {
          throw new Error('Response is not in JSON format');
        }
  
        setPost(res.data);
      } catch (err) {
        console.log(err);
        console.error('Erreur Axios:', err.message);
      }
    };
    fetchData();
  }, [postId]);  

  // Fonction qui gre la suppression de l'article, elle envoie une requte HTTP DELETE  l'API pour supprimer l'article d'id correspondant. Aprs la suppression, l'utilisateur est redirig vers la page d'accueil
  
  // Version locale
  
  // const handleDelete = async ()=>{
  //   try {
  //     await axios.delete(`/posts/${postId}`);
  //     navigate("/")
  //   } catch (err) {
  //     console.log(err);
  //   }
  // }


  // Version serveur

  const handleDelete = async ()=>{
    let url = `http://gabcassblog.gabriel-cassano.be/api/posts/${postId}`;  
        
    try {
      await axios.delete(url);
      navigate("/")
    } catch (err) {
      console.log(err);
    }
  }

  // Fonction prenant une chane de caractres HTML en entre et retournant le texte brut extrait du code HTML en utilisant "DOMParser"
  const getText = (html) =>{
    const doc = new DOMParser().parseFromString(html, "text/html")
    return doc.body.textContent
  }

  return (
    <div className="single">
        <div className="content">
          <div>
            <img src={`../upload/${post?.img}`} alt="" />
          </div>

          <div className="user">
            {post.userImg && <img
              src={`../upload/${post.userImg}`}
              alt="userImg"
            />}
            <div className="info">
              <span>Publi par {post.username}</span>
              <p>Il y a {formatTimeSincePublication(post.date)}</p>
            </div>
            {currentUser && currentUser.username === post.username && (
              <div className="edit">
                <Link to={`/write?edit=2`} state={post}>
                  <img src={Edit} alt="" />
                </Link>
                <img onClick={handleDelete} src={Delete} alt="" />
              </div>
            )}
          </div>
          <h1>{post.title}</h1>
          <p
            dangerouslySetInnerHTML={{
              __html: DOMPurify.sanitize(post.desc),
            }}
          ></p>      </div>
      <Menu cat={post.cat}/>
    </div>
  );
};

// Exportation du composant dans d'autres partie de l'application
export default Single;
