import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import axios from "axios";

const Home = () => {
  const [posts, setPosts] = useState([]);

  const cat = useLocation().search

  // Version locale 

  // useEffect(() => {
  //   const fetchData = async () => {
  //     try {
  //       const res = await axios.get(`/posts/${cat}`);
  //       setPosts(res.data);

  //      // console.log(res.data);
  //     } catch (err) {
  //       console.log(err);
  //     }
  //   };
  //   fetchData();
  // }, [cat]);

  // Version production

  useEffect(() => {
    const fetchData = async () => {
      try {
        let url = 'api/posts'; // URL par dfaut pour rcuprer tous les posts
        
        // Vrifier si cat est dfini, sinon rcuprer tous les posts
        if (cat) {
          url = `api/posts/${cat}`;
        }
        
        const res = await axios.get(url);
  
        if (!res.headers['content-type'] || !res.headers['content-type'].includes('application/json')) {
          throw new Error('Response is not in JSON format');
        }
  
        setPosts(res.data);
      } catch (err) {
        console.log(err);
        console.error('Erreur Axios:', err.message);
      }
    };
    fetchData();
  }, [cat]);  
 
  const getText = (html) =>{
    const doc = new DOMParser().parseFromString(html, "text/html")
    return doc.body.textContent
  }

 {/*
    Conteneur de messages 
    *********************

    Pour chaque message, on va retourner les dtails 
    ************************************************
    On utilise ".map()", on donne une cl unique, qui est l'identifiant de post, tous les identifiants sont diffrents, on peut les utiliser et  l'intrieur de la div, on aura un conteneur d'image qui va inclure l'image, et on aura un contenu. Cela va inclure le titre et la description.

  */}
  return (   
    <div className="home">
      <div class="alert-danger"> Ce site est un site de dmonstration, contenant des donnes fictives, et ayant pour unique objectif, de prsenter mes comptences en React JS et Node JS</div>
      <div className="posts">
      {Array.isArray(posts) && 
          posts.map((post) => (
            <div className="post" key={post.id}>
              <div className="img">
                <img src={`../upload/${post.img}`} alt="" />
              </div>
              <div className="content">
                {/* Rcupration de la publication par l'id  */}
                <Link className="link" to={`/post/${post.id}`}>
                   {/* Rcupration du titre de la publication */}
                  <h1>{post.title}</h1>
                </Link>
                {/* Rcupration de la description de la publication */}
                <p>{getText(post.desc)}</p>
                 {/* Bouton qui permettra de lire toute la description */}
               {/* <button>Lire plus</button> */}
              </div>
            </div>
          ))
        }
      </div>
    </div>
  );
};

export default Home;